	module.exports = {"data":{"markdownRemark":{"html":"<p>HackerOne enables you to temporarily pause new report submissions for your program to give you time to address existing reports. If your program has alerts that are failing response SLAs, automated alerts will suggest pausing report submissions.</p>\n<p>To pause submissions:</p>\n<ol>\n<li>Go to <strong>Program Settings > Program > Customization > Submit Report Form</strong>.</li>\n<li>Select <strong>No</strong> for <strong>Accepting new report submissions</strong>. </li>\n</ol>\n<p>When you pause report submissions for your program, HackerOne:</p>\n<ul>\n<li>Communicates this to potential hackers with a message notification on your program's security page.</li>\n<li>Disables the <strong>Submit Report</strong> button on your security page</li>\n</ul>\n<p>Programs can re-enable report submissions as long as they don't currently have reports that are failing to meet response SLAs.</p>\n<p>For programs that don't meet response SLA limits, HackerOne may temporarily pause new report submissions for you. When a program fixes all of their failing SLA reports, report submissions will automatically resume.</p>","frontmatter":{"path":"/programs/pausing-report-submissions.html","id":"programs/pausing-report-submissions","title":"Pausing Report Submissions","description":null}}},"pathContext":{}}

